* {box-sizing: border-box;}

.ShopItem{
  float: left;
  display: block;
  color: #ffffff;
  text-align: center;
  justify-self: center;
  margin-top: 2%;
  margin-left: 2%;
  margin-bottom: 0.2%;
  text-decoration: none;
  overflow: hidden;
  width: 200px;
  height: 200px;
}
.ShopItemPic{
    display: flex;
    justify-content: center;
    height: 200px;
    width: 200px; 
    overflow: hidden;
}
.ShopItemDiscription{

    position:relative;
    top: -200px;
    opacity: 0;
    background: linear-gradient(to bottom, transparent, rgb(5, 97, 92));
    width: 200px;
    height: 200px;
    vertical-align: sub;

}
.ShopItemDiscription:hover{
    animation-name: example;
    animation-duration: 0.5s;
    opacity: 1;
}
.ShopItemName{
    position: relative;
    top: 130px;
    font-size: 24px;
    font-style: italic;
    text-transform: capitalize;
}
.ShopItemPrice{
    position: relative;
    top: 140px;

}
.ShopContainer{
    width: 95%;
}



/* Button used to open the contact form - fixed at the bottom of the page */
.open-button {
  background-color: #555;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  opacity: 0.8;
  position: fixed;
  top: 23px;
  right: 28px;
  width: 10%;
}

/* The popup form - hidden by default */
.form-popup {
  display: none;
  position: fixed;
  top: 0;
  right: 15px;
  border: 3px solid #f1f1f1;
  z-index: 9;
}

/* Add styles to the form container */
.form-container {
  max-width: 300px;
  padding: 10px;
  background-color: white;
}

/* Full-width input fields */
.form-container input[type=text], .form-container input[type=password] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  border: none;
  background: #f1f1f1;
}

/* When the inputs get focus, do something */
.form-container input[type=text]:focus, .form-container input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}

/* Set a style for the submit/login button */
.form-container .btn {
  background-color: #04AA6D;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-bottom:10px;
  opacity: 0.8;
}

/* Add a red background color to the cancel button */
.form-container .cancel {
  background-color: red;
}

/* Add some hover effects to buttons */
.form-container .btn:hover, .open-button:hover {
  opacity: 1;
} 



@keyframes example {
    from {opacity: 0;}
    to {opacity: 1;}
  }